import PropTypes from 'prop-types'
import Button from './Button';
const Header = ({title, age, showForm, buttonText,color }) => {

  
    console.log(showForm)
    return (
        <header className='header'>
            <h1>{title}</h1>
            <Button  
            color = {color ? 'hotpink' : 'green'} 
            title = {buttonText} 
            onClick = { showForm } 

            />
        </header>
    )
}

Header.defaultProps = {
    title : 'Baburao',
    age: 21
}

// CSS IN JS
// const HeaderStyles = {
//     color: 'green', 
//     backgroundColor: 'olive', 
//     borderRadius: '10px'
// }



Header.propTypes = {
    title: PropTypes.string.isRequired
}
export default Header
