
import { useState } from 'react'

import  Header  from "./components/Header.jsx";
import Tasks from "./components/Tasks.jsx";
import AddTask from "./components/AddTask.jsx";

function App() {
  const [tasks, setTasks] = useState([
    {
        "id": 1,
        "text": "Doctors Appointment",
        "day": "Feb 5th at 2:30pm",
        "reminder": false
    },
    {
        "id": 2,
        "text": "Meeting at School",
        "day": "Feb 6th at 1:30pm",
        "reminder": true
    }
])


////Button Toggle for Form
const [showAddBtn, setAddBtn] = useState(false)

///Add Task 

  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000 + 1)
    const newTask = {id, ...task}

    setTasks([...tasks, newTask])
  }

  const deleteTask = (id) => {
    setTasks(
      tasks.filter((task) => task.id !== id)
    )
  }

  const doubleClick = (id) => {
   setTasks(
     tasks.map(task => (
       task.id === id ?
       {...task, reminder: !task.reminder} : task
     ))
   )
  }

  return (

    <div className="container">
    <Header title='allan' showForm = { () => setAddBtn(!showAddBtn) } buttonText = {showAddBtn} color = {showAddBtn} />
    {showAddBtn && <AddTask onAdd = { addTask } />}
    {
      tasks.length > 0 ? <Tasks tasks={tasks}  onDelete = { deleteTask } onDouble = {doubleClick}/> : 'No tasks right Now, We Good !ðŸ¥³'
    }   
  
    </div>
  );
}

export default App;
